import { FC } from "react";
import { CardItemService } from "./card_item_service";
import { ICardServices } from "../../mock/card_params";

interface ICardService {
    linkText: string;
    cardServices: ICardServices[];
}

export const CardService: FC<ICardService> = ({cardServices,linkText}) => {
    return (
        <div className="flex flex-wrap  gap-y-5 justify-around lg:justify-between lg:gap-x-[6px] lg:gap-y-8 ">
            {cardServices.map((props, idx) => (
                <CardItemService
                    {...props}
                    linkText={linkText}
                    key={`service_${props.title}_${idx}`}
                />
            ))}
        </div>
    );
};
