import clsx from "clsx";
import { LinkArrow } from "../link_arrow";

interface IImgPluseText {
    to?: string;
    text: string;
    mockImg: string;
    linkText?: string;
    className?: string;
    classNameImg?: string;
    classNameLink?: string;
    classNameText?: string;
}

export const ImgPluseText: React.FC<IImgPluseText> = ({
    to,
    text,
    mockImg,
    linkText,
    className,
    classNameImg,
    classNameLink,
    classNameText,
}) => {
    const textBr = text.replace(/([.])\s+/g, "$1<br/>");
    return (
        <div
            className={`md:flex md:flex-auto md:gap-5 flex flex-col items-center justify-center md:justify-between md:flex-row ${className}`}
        >
            <div className="max-w-[400px] w-full lg:min-w-[380px] ">
                <img
                    alt="/"
                    src={mockImg}
                    className={classNameImg}
                />
            </div>
            <div>
                <span
                    dangerouslySetInnerHTML={{ __html: textBr }}
                    className={clsx(
                        "sm:text-[14px] text-[12px] md:text-[16px]  font-montserrat",
                        classNameText
                    )}
                />

                {to && linkText && (
                    <LinkArrow
                        to={to}
                        linkText={linkText}
                        classNameLink={classNameLink}
                    />
                )}
            </div>
        </div>
    );
};
