import { Link } from "react-router-dom";
import { scrollToTop } from "../../../../helpers";

interface INavItems {
    name: string;
    path: string;
    idx?: number;
    textClass: string;
    setIsActive: React.Dispatch<React.SetStateAction<boolean>>;
}

export const NavItems: React.FC<INavItems> = ({
    name,
    path,
    textClass,
    setIsActive,
}) => {
    const handler = () => {
        scrollToTop("auto");
        setIsActive(false);
    };

    return (
        <Link
            to={path}
            onClick={handler}
        >
            <h1 className={`${textClass} `}>{name}</h1>
        </Link>
    );
};
