import { Link } from "react-router-dom";
import { Arrow } from "../../assets";
import clsx from "clsx";

interface ILinkArrow {
    linkText?: string;
    to: string;
    classNameLink?: string;
    className?: string;
    fill?: string;
}

export const LinkArrow: React.FC<ILinkArrow> = ({
    to,
    fill,
    linkText,
    className,
    classNameLink,
}) => {
    return (
        <Link
            to={to}
            className={clsx("flex gap-x-3 items-center ", classNameLink)}
        >
            <p
                className={clsx(
                    "text-secondTextColor text-[17px] font-openSans font-semibold ",
                    className
                )}
            >
                {linkText}
            </p>
            <Arrow
                className="mt-[2px]"
                fill={fill ? fill : "#2875FF"}
            />
        </Link>
    );
};
