interface IButton {
    text: string;
    btnClass: string;
    onClick?: () => null;
    type?: "button" | "submit" | "reset";
}

export const Button: React.FC<IButton> = ({
    text,
    btnClass,
    onClick,
    type,
}) => {
    return (
        <button
            className={btnClass}
            onClick={onClick}
            type={type}
        >
            {text}
        </button>
    );
};
