import clsx from "clsx";

interface ISubTitle {
    subTitleClass?: string;
    text: string;
    colorText?: string;
    colorSubTitleClass?: string;
}

export const SubTitle: React.FC<ISubTitle> = ({
    subTitleClass,
    text,
    colorText,
    colorSubTitleClass,
}) => {
    return (
        <h2 className={clsx("text-xl lg:text-5xl pb-[60px]", subTitleClass)}>
            <span>{text}</span>

            <span
                className={clsx(
                    "text-xl text-blue-500 lg:text-[48px]",
                    colorSubTitleClass
                )}
            >
                &nbsp; {colorText}
            </span>
        </h2>
    );
};
