import { createBrowserRouter } from "react-router-dom";

import {
    User,
    About,
    Admin,
    Layout,
    Service,
    Register,
    Technologies,
} from "../pages";
import { routes } from "../constants";
import { Home } from "../pages/home";

export const router = createBrowserRouter([
    {
        path: routes.home,
        element: <Layout />,
        children: [
            {
                index: true,
                element: <Home />,
            },
            {
                path: routes.services,
                element: <Service />,
            },
            {
                path: routes.aboutUs,
                element: <About />,
            },
            {
                path: routes.technologies,
                element: <Technologies />,
            },
            {
                path: routes.register,
                element: <Register />,
            },

            {
                path: routes.user,
                element: <User />,
            },
            {
                path: routes.admin,
                element: <Admin />,
            },
        ],
    },
]);
