import { Field, FieldProps } from "formik";
import { ChangeEvent, FC } from "react";

interface IInputField {
    type: string;
    placeholder: string;
    name: string;
}

export const InputField: FC<IInputField> = ({ type, placeholder, name }) => {
    const handleChange = (
        event: ChangeEvent<HTMLInputElement>,
        setFieldValue: (
            value: any,
            field: string,
            shouldValidate?: boolean
        ) => void,
        typeInput: string
    ) => {
        typeInput === "text" && setFieldValue(name, event.target.value);
    };

    return (
        <Field name={name}>
            {({ form: { setFieldValue }, field, meta }: FieldProps) => {
                return (
                    <div className="p-2 shadow-lg rounded-md border-2 border-gray-100">
                        <input
                            className=" p-2 "
                            type={type}
                            placeholder={placeholder}
                            value={field.value}
                            onChange={(event) =>
                                handleChange(event, setFieldValue, type)
                            }
                        />

                        {meta.touched && meta.error && (
                            <div className="error">{meta.error}</div>
                        )}
                    </div>
                );
            }}
        </Field>
    );
};
