import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination } from "swiper/modules";

import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";

import "./style.css";
import { Link } from "react-router-dom";
import { IMockSlide } from "../../mock/swiper_sliders";

interface ISlider {
    mockSlide: IMockSlide[];
}

export const Slider: React.FC<ISlider> = ({ mockSlide }) => {
    return (
        <Swiper
            spaceBetween={30}
            navigation={true}
            pagination={{ clickable: true }}
            breakpoints={{
                320: {
                    slidesPerView: 1,
                },
                768: {
                    slidesPerView: 2,
                },
            }}
            modules={[Navigation, Pagination]}
        >
            {mockSlide &&
                mockSlide.map(({ img, to }, idx) => (
                    <SwiperSlide key={`swiper_${idx}`}>
                        <Link
                            to={to}
                            className="filter grayscale"
                        >
                            <img
                                src={img}
                                alt=""
                            />
                        </Link>
                    </SwiperSlide>
                ))}
        </Swiper>
    );
};
