import { NavItems } from "./nav_items/inde";

import { headerParams } from "../../../mock/header_params";

interface INavbar {
    className: string;
    textClass: string;
    setIsActive: React.Dispatch<React.SetStateAction<boolean>>;
}

export const Navbar: React.FC<INavbar> = ({
    className,
    textClass,
    setIsActive,
}) => {
    return (
        <nav className={className}>
            {headerParams.nav.map(({ name, path }, idx) => (
                <NavItems
                    name={name}
                    path={path}
                    idx={idx}
                    textClass={textClass}
                    setIsActive={setIsActive}
                    key={`nav_${name}_${idx}`}
                />
            ))}
        </nav>
    );
};
